

import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
components: {
XHeader,
ButtonTab,
ButtonTabItem,
Group,
XInput,
Cell,
PopupPicker,
Datetime,
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
cardList: [['6210 **** **** 1219', '6210 **** **** 3341', '6210 **** **** 2567']],
tabSelected: 0,
tabShow: '1',
cardNo: ['6210 **** **** 1219'], // 卡号
cardInfoList: [
{
accno: '',
accno_show: '6210 **** **** 1219',
cartType: '储蓄卡',
balance: '23000.00',
accountType: 'Ⅰ类'
}
],
currType: '人民币',
startDateQuery: '2018-04-20',
endDateQuery: '2018-05-20'
}
},
created () {

},
computed: {
},
watch: {
$router (now, old) {
}
},
methods: {
submit (val) {
this.tipsShow = true
},
gotoQuery () {

this.$router.push({path: 'TransactionDetails', query: {cardNo: this.cardNo[0], accno: this.accno, startDate: this.startDateQuery, endDate: this.endDateQuery}})
},
changeStart (val) {
this.startDateQuery = val
},
changeEnd (val) {
this.endDateQuery = val
},
addAccount () {
console.log('添加银行卡')
},
btnClick (type, index) {
switch (type) {
case 'info':
this.$router.push({path: 'AccountDetails', query: {accno: ''}})
break
case 'week':
this.tabSelected = 1
this.tabShow = '2'
break
case 'history':
this.tabSelected = 1
this.tabShow = '2'
break
default: break
}
}
}
}



